/* src/pages/HomePage.css */

/* Pastel color palette */
:root {
  --pastel-green: #a8dadc;
  --pastel-pink: #f8c8dc;
  --pastel-blue: #bde0fe;
  --pastel-yellow: #ffeeba;
  --text-dark: #333;
  --white: #fff;
}

.home-container {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--pastel-blue);
  min-height: 100vh;
  padding: 2rem;
  text-align: center;
}

.home-header {
  margin-bottom: 2rem;
}

.home-header h1 {
  font-size: 2.5rem;
  color: var(--text-dark);
}

.home-header p {
  color: #555;
  font-size: 1.1rem;
}

.home-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.home-card {
  background-color: var(--white);
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  width: 280px;
  transition: transform 0.2s ease;
}

.home-card:hover {
  transform: translateY(-5px);
}

.home-card h2 {
  color: var(--text-dark);
  margin-bottom: 0.5rem;
}

.home-card p {
  color: #555;
  font-size: 0.95rem;
}

.home-btn-container {
  margin-top: 1rem;
}

.home-btn {
  background-color: var(--pastel-green);
  color: var(--text-dark);
  padding: 0.8rem 1.5rem;
  text-decoration: none;
  font-weight: bold;
  border-radius: 25px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.home-btn:hover {
  background-color: var(--pastel-pink);
  transform: scale(1.05);
}
